<template>
  <div id="single-blog">
    <nav-bar></nav-bar>
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="(category, index) in blog.categories" :key="index">
        {{category}}
      </li>
    </ul>
    <button class="delete" @click="deleteBlog()">删除</button>
    <router-link :to="'/edit/' + id" class="edit">编辑</router-link>
  </div>
</template>

<script>
import NavBar from './navbar/NavBar'

import { request } from "../network/request";

export default {
  name: "SingleBlog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {
      }
    }
  },
  components: {
    NavBar
  }, 
  created() {
    request({
      url: '/posts' + '/' + this.id + '.json',
      method: 'get'
    }).then(item => {
      console.log(item)
      this.blog = item.data
    })
  },
  methods: {
    deleteBlog() {
      request({
        url: '/posts' + '/' + this.id + '.json',
        method: 'delete'
      }).then(response => {
        this.$router.push({path: '/home'})
      })
    }
  }
}
</script>

<style scoped>
  #single-blog {
    margin: 72px auto;
    max-width: 680px;
    padding: 0 20px 20px 20px;
    background-color: rgb(237, 255, 220);
    border: 1px dotted rgb(237, 255, 220);
    border-radius: 20px;
  }
  .delete {
    background-color: rgb(253, 228, 255);
    border: 1px dotted rgb(237, 255, 220);
    border-radius: 20px;
    padding: 10px;
    color: #444;
    font-size: 16px;
  }
  .edit {
    text-decoration: none;
    background-color: rgb(253, 228, 255);
    border: 1px dotted rgb(237, 255, 220);
    border-radius: 20px;
    padding: 10px;
    color: #444;
  }
</style>


